.Header_hamburger__2ba6n {
  /* width: 55px; */
  margin: auto 0;
  margin-right: 15px;
  /* border: 3px solid orange; */
  display: none;
  /* margin: auto; */
}

.Header_hamburger__2ba6n > div {
  width: 20px;
  height: 2px;
  background-color: black;
  margin: 4px;
