}

@media screen and (max-width: 1024px) {
  body {
    overflow-x: hidden;
  }
  .Header_hamburger__2ba6n {
    display: block;
  }
  .Header_navbarContainer__3FrhP {
    height: 6vh;
    align-items: center;
  }
  .Header_navbarWrapper__1GJyU {
    position: fixed;
    top: 6vh;
    right: 0;
    width: 100%;
    height: 94vh;
    transform: translateX(100%);
    transition: transform 0.2s ease-in;
    background-color: white;
  }
  .Header_navbarLogo__16pVt {
    letter-spacing: 2px;
  }

  .Header_navbar__22m2v {
    margin: 0;
    padding: 0;
    flex-direction: column;

    width: 100%;
    height: 94vh;
    font-size: 24px;
    justify-content: unset;
  }
  .Header_navbarText__1fUrA {
    color: rgb(9, 31, 67);

    display: inline-block;
    margin: 2vh;
  }
}

.Header_navbarActive__1eooH {
  transform: translateX(0%);
}

.Header_navbarScroll__2eT9z {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  /* width: 100%; */
  /* padding-left: 2vh; */
  /* background-color: white; */
  box-shadow: 0px 1px 8px -5px rgba(0, 0, 0, 0.836);
  /* transition: all 0.1s ease; */
  transition: 0.8s all ease;
  /* background-color: #e9ebec; */
}

.Header_toggle__3M8PS .Header_arrow1__10bA5 {
  transform: rotate(-45deg) translate(-5px, 5px);
}

.Header_toggle__3M8PS .Header_arrow2__2sgUw {
  opacity: 0;
}

.Header_toggle__3M8PS .Header_arrow3__buihp {
  transform: rotate(45deg) translate(-3px, -4px);
}

